.wrapper {
    width: 300px;
    padding: 20px;
    margin-left: 30px;
    background-color: rgba(0, 0, 0, .65);
    border-radius:5px;
    color: #fff;
}
.list__container {
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.list__item {
    padding: 5px;
    margin-bottom: 7px;
}
.item__title {
color: rgb(184, 52, 74);
}

@media screen and (max-width: 700px) {
    .wrapper {
        width: 100%;
        margin: 0;
        margin-top: 20px;
    }
}